import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import icBeenhere from '@iconify/icons-ic/twotone-beenhere';
import icBusinessCenter from '@iconify/icons-ic/twotone-business-center';
import icMail from '@iconify/icons-ic/twotone-mail';
import icPhoneInTalk from '@iconify/icons-ic/twotone-phone-in-talk';
import icStars from '@iconify/icons-ic/twotone-stars';

import { fadeInUp400ms, stagger60ms } from '@vex/animations';
import theme from '@vex/utils/tailwindcss';

import { UserResource } from '@core/http';
import { Organization, Plan, Subscription } from '@data/schema';
import { LayoutConfigService } from '@layout/services/layout-config.service';
import { UserDataService } from '@users/services/user-data.service';

@Component({
  selector: 'vex-pricing',
  templateUrl: './pricing.component.html',
  styleUrls: ['./pricing.component.scss'],
  animations: [
    stagger60ms,
    fadeInUp400ms,
  ],
})
export class PricingComponent implements OnInit, OnDestroy {
  organizations: Organization[];
  plans: Plan[];
  subscription: Subscription;

  icBeenhere = icBeenhere;
  icStars = icStars;
  icBusinessCenter = icBusinessCenter;
  icPhoneInTalk = icPhoneInTalk;
  icMail = icMail;

  theme = theme;

  constructor(
    private layoutConfigService: LayoutConfigService,
    private route: ActivatedRoute,
    private router: Router,
    private userResource: UserResource,
    private userDataService: UserDataService
  ) {
    this.layoutConfigService.setIkaros();
  }

  ngOnInit() {
    this.plans = this.route.snapshot.data.plans;
    this.subscription = this.route.snapshot.data.subscription;
  }

  buy(plan: Plan) {
    const snapshot = this.route.snapshot;
    const organizationId = snapshot.queryParams.organization_id;

    if (!organizationId) {
      const user = this.userDataService.user;

      if (!user) {
        // If the user is not logged in, route then to login page
        this.router.navigate(['/login']);
      } else {
        this.userResource.profile(
          { organization: true }
        ).subscribe(
          (res: UserResponse) => {
            const { organization_id: organizationId } = res;
            this.showBuy(plan, organizationId);
          }
        );
      }
    } else {
      this.showBuy(plan, snapshot.queryParams.organization_id);
    }
  }

  showBuy(plan: Plan, organizationId: Organization['id']) {
    const path = '/subscriptions/buy';

    this.router.navigate([path], {
      queryParams: {
        organization_id: organizationId,
        plan_id: plan.id,
      },
    });
  }

  isSubscribed(plan: Plan) {
    if (!this.subscription) {
      return false;
    }

    return plan.id == this.subscription.plan.id;
  }

  ngOnDestroy() {
    this.layoutConfigService.setZeus();
  }
}
